<template>
  <el-container>
    <el-aside class="sideBar">
      <el-col>
        <h2>Movie DB</h2>
        <el-menu
          default-active="1"
          @select="menuSelect"
          background-color="#313a46"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="1">
            <i class="el-icon-menu"></i>
            <span slot="title">全部</span>
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-document"></i>
            <span slot="title">电影</span>
          </el-menu-item>
          <el-menu-item index="3">
            <i class="el-icon-document"></i>
            <span slot="title">剧集</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col>
        <el-tag
          class="filterTag"
          :key="tag"
          v-for="tag in filterTags"
          closable
          :disable-transitions="false"
          @close="closeTag(tag)"
        >
          {{ tag }}
        </el-tag>
        <el-input
          class="inputNewTag"
          v-if="tagInputVisible"
          v-model="tagInputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleTagInputConfirm"
          @blur="handleTagInputConfirm"
        >
        </el-input>
        <el-button
          v-else
          class="button-new-tag"
          size="small"
          @click="showTagInput"
          >+ 输入标签</el-button
        >
      </el-col>
    </el-aside>
    <el-container>
      <el-header>
        <el-col class="optionModule" :span="2" :xs="3">
          <el-popover
            ref="popover"
            placement="right"
            width="200"
            trigger="click"
          >
            <div>
              <div id="topTags" class="hotTag">
                <p>热门标签：</p>
                <el-tag
                  class="movieTag"
                  v-for="tag in hotTags"
                  :key="tag"
                  @click="addTag(tag)"
                  >{{ tag }}
                </el-tag>
              </div>
              <el-tag
                class="filterTag inlineTag"
                :key="tag"
                v-for="tag in filterTags"
                closable
                :disable-transitions="false"
                @close="closeTag(tag)"
              >
                {{ tag }}
              </el-tag>
              <el-input
                class="inputNewTag"
                v-if="tagInputVisible"
                v-model="tagInputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleTagInputConfirm"
                @blur="handleTagInputConfirm"
              >
              </el-input>
              <el-button
                v-else
                class="button-new-tag"
                size="small"
                @click="showTagInput"
                >+ 输入标签</el-button
              >
            </div>
            <el-button
              class="optionButton"
              icon="el-icon-menu"
              circle
              slot="reference"
            ></el-button>
          </el-popover>
        </el-col>
        <el-col :span="4" :xs="10" :sm="6" :md="6" class="searchBar">
          <el-input
            placeholder="输入电影名称"
            v-model="q"
            suffix-icon="el-icon-search"
            clearable
            @input="search"
          ></el-input>
        </el-col>
        <el-col
          :span="5"
          :offset="10"
          :xs="{ span: 10, offset: 0 }"
          :sm="{ span: 8, offset: 6 }"
          :md="{ span: 7, offset: 9 }"
          class="orderMenu"
        >
          <div>
            <el-button
              :class="{ activeText: order_by === '-update_date' }"
              type="text"
              @click="sort('-update_date')"
              >按上新</el-button
            >
            <el-button
              :class="{ activeText: order_by != '-update_date' }"
              type="text"
              @click="sort('-douban_rating')"
              >按评分</el-button
            >
          </div>
        </el-col>
      </el-header>

      <el-main>
        <transition name="el-fade-in-linear">
          <el-row class="movieList" :gutter="20" v-show="movieShow">
            <el-col
              :span="8"
              :md="6"
              :lg="4"
              :xl="3"
              v-for="movie in movies"
              :key="movie.id"
              class="movieCard"
            >
              <div class="movieItem">
                <el-popover
                  class="pcPoster"
                  :open-delay="400"
                  placement="right"
                  width="400"
                  trigger="hover"
                >
                  <div class="infomer">
                    <div class="intro">
                      {{ movie.intro }}
                    </div>
                    <div>
                      <el-tag
                        class="movieTag"
                        v-for="tag in movie.tags"
                        :key="tag"
                        @click="addTag(tag)"
                        >{{ tag }}</el-tag
                      >
                    </div>
                    <a class="uri" target="_blank" :href="movie.douban_url">
                      <el-button size="mini" type="success" plain
                        >豆瓣</el-button
                      >
                    </a>
                  </div>
                  <div
                    slot="reference"
                    class="poster"
                    @click="goToMovie(movie.id)"
                    :style="
                      'background-image: url(' + movie.thumbnail_url + ')'
                    "
                  ></div>
                </el-popover>
                <div
                  slot="reference"
                  class="poster mobilePoster"
                  @click="goToMovie(movie.id)"
                  :style="'background-image: url(' + movie.thumbnail_url + ')'"
                ></div>
              </div>

              <div class="movieInfo">
                <p class="movieTitle" :title="movie.title">
                  {{
                    movie.title +
                    (movie.original_title !== "" ? "&nbsp;/&nbsp;" : "") +
                    movie.original_title
                  }}
                  <span class="movieYear">&nbsp;( {{ movie.year }})</span>
                </p>
                <div class="doubanRating">
                  <label class="豆瓣评分："></label
                  ><el-rate
                    :value="movie.douban_rating / 2"
                    disabled
                    text-color="#ff9900"
                    disabled-void-color=""
                  >
                  </el-rate>
                  <i class="ratingText"> {{ movie.douban_rating }} </i>
                </div>
              </div>
            </el-col>
            <span v-if="loading">加载中...</span>
          </el-row>
        </transition>
      </el-main>
    </el-container>
  </el-container>
</template>


<script>
// import func from "../../vue-temp/vue-editor-bridge";
const axios = require("axios");
export default {
  name: "home",
  data() {
    return {
      api: process.env.VUE_APP_API,
      token: "",
      loading: false,
      movies: [],
      type: "",
      page: 1,
      limit: 24,
      q: "",
      order_by: "-update_date",
      movieShow: false,
      noMore: false,
      filterTags: [],
      tagInputVisible: false,
      tagInputValue: "",
      hotTags: [],
    };
  },
  methods: {
    // 初始化页面
    init: function () {
      if (localStorage.token) {
        this.token = localStorage.token;
        axios.defaults.headers.common["Authorization"] = `JWT ${this.token}`;
        this.getHotTags();
        this.getMovies();
      } else {
        this.getToken();
      }
    },
    getToken: function () {
      let vm = this;
      vm.$prompt("请输入账号密码（账号:密码）", "认证", {
        confirmButtonText: "确定",
        inputPattern: /.*:.*?/,
        inputErrorMessage: "格式不正确",
      })
        .then(({ value }) => {
          let params = value.split(":");
          axios
            .post("/api/auth", {
              username: params[0],
              password: params[1],
            })
            .then(function (response) {
              localStorage.token = response.data.access_token;
              vm.token = localStorage.token;
              axios.defaults.headers.common[
                "Authorization"
              ] = `JWT ${vm.token}`;
              vm.getHotTags();
              vm.getMovies();
            })
            .catch(function (error) {
              vm.$message({
                type: "info",
                message: "账号密码错误，刷新重试",
              });
              console.log(error);
            });
        })
        .catch(() => {});
    },
    // 监听滚动条
    listenScoller: function () {
      let vm = this;
      window.onscroll = function () {
        if (document.getElementsByClassName("movieList").length == 0) {
          return;
        }
        let scrollHeight = Math.max(
          document.documentElement.scrollHeight,
          document.body.scrollHeight
        );
        let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
        let clientHeight = window.innerHeight || document.body.clientHeight;
        if (clientHeight + scrollTop >= scrollHeight - 10) {
          vm.infLoad();
        }
      };
    },
    // 重置请求参数
    reset: function () {
      this.movieShow = false;
      this.movies = [];
      this.type = "";
      this.page = 1;
      this.limit = 40;
      this.q = "";
      this.order_by = "-update_date";
    },
    // 标签变化刷新页面
    refreshByTag: function () {
      this.page = 0;
      this.movies = [];
      this.getMovies();
    },
    // 获取电影数据
    getMovies: function () {
      let vm = this;
      vm.loading = true;
      setTimeout(function () {
        axios
          .get(
            `/api/movies?page=${vm.page}&limit=${vm.limit}&q=${
              vm.q
            }&year=&type=${vm.type}&order_by=${
              vm.order_by
            }&tags=${vm.filterTags.join(",")}`
          )
          .then(function (response) {
            if (response.data.length == 0) {
              vm.noMore = true;
            }
            response.data.forEach((movie) => {
              vm.movies.push(movie);
              vm.movieShow = true;
            });
            setTimeout(function () {
              vm.loading = false;
            }, 1200);
          })
          .catch(function (error) {
            console.log(error);
            vm.getToken();
          });
      }, 1000);
    },
    // 菜单选择重新获取数据
    menuSelect: function (index) {
      this.reset();
      if (index === "3") {
        this.type = "剧集";
      } else if (index === "2") {
        this.type = "电影";
      } else {
        this.type = "";
      }
      this.getMovies();
    },
    // 滚动条变化加载新数据
    infLoad: function () {
      if (this.noMore || this.loading) {
        return;
      }
      this.page++;
      this.getMovies();
    },
    // 电影排序
    sort: function (key) {
      this.reset();
      this.order_by = key;
      this.getMovies();
    },
    //电影搜索
    search: function () {
      this.page = 1;
      this.movies = [];
      this.getMovies();
    },
    // 跳转电影对应页面
    goToMovie: function (id) {
      this.$router.push({ path: `/movie/${id}` });
    },
    //新增筛选标签
    addTag: function (tag) {
      if (this.filterTags.indexOf(tag) === -1) {
        this.filterTags.push(tag);
        this.refreshByTag();
      }
    },
    // 去除筛选标签
    closeTag: function (tag) {
      let index = this.filterTags.indexOf(tag);
      this.filterTags.splice(index, 1);
      this.refreshByTag();
    },
    // 显示标签输入框
    showTagInput: function () {
      this.tagInputVisible = true;
      this.$nextTick((_) => {
        console.log(_);
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    // 获取热门标签
    getHotTags: function () {
      let vm = this;
      axios
        .get("/api/tags/top")
        .then(function (response) {
          response.data.forEach(function (tag) {
            vm.hotTags.push(tag[0]);
          });
        })
        .catch(function (error) {
          console.log(error);
          vm.getToken();
        });
    },
    // 输入标签确认
    handleTagInputConfirm: function () {
      let inputValue = this.tagInputValue;
      if (inputValue !== "" && this.filterTags.indexOf(inputValue) === -1) {
        this.filterTags.push(inputValue);
        this.refreshByTag();
      }
      this.tagInputVisible = false;
      this.tagInputValue = "";
    },
  },
  mounted() {
    this.init();
    this.listenScoller();
  },
};
</script>

