import VueRouter from 'vue-router'
import Vue from 'vue'
import Home from '../components/Home'
import Movie from '../components/Movie'
 
Vue.use(VueRouter)
 
const routes = [
  {
    path: '',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home,
    meta:{
      keepAlive:true,
    }
  },
  {
    path: '/movie/:id',
    component: Movie,
    meta:{
      keepAlive:false,
    }
  }
]
const router = new VueRouter({
  routes,
  mode: 'hash',
  linkActiveClass: 'active'
})
 
export default router