<template>
  <el-container>
    <el-header>
      <el-col :span="12" :lg="11">
        <div class="backButton">
          <el-button icon="el-icon-back" circle @click="goBack()"></el-button>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="playOption">
          <el-select
            v-model="playType"
            placeholder="选择App"
            @change="changePlayType"
          >
            <el-option
              v-for="item in playOptions"
              :key="item.label"
              :label="item.label"
              :value="item.label"
            >
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-header>
    <el-main>
      <el-dialog title="提示" :visible.sync="tipVisible">
        <p>
          Windows下在页面打开PotPlayer播放需注册协议，本提示不会再次弹出(除非删掉浏览器本地存储中的pc_tip)。
        </p>
        <p>需要输入PotPlayer路径：</p>
        <el-input v-model="potplayer" autocomplete="off"></el-input>
        <p>复制以下内容另存为potplayer.reg，双击合并到注册表。</p>
        <el-input type="textarea" :rows="5" placeholder="" v-model="pcRegCode">
        </el-input>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="tipVisible = false"
            >确 定</el-button
          >
        </div>
      </el-dialog>
      <transition name="el-fade-in-linear">
        <div>
          <el-col :span="24" :lg="18">
            <div class="movieInfo">
              <!-- <div class="movieTitle">{{ fullTitle }}</div> -->
              <div v-html="descHtml" class="doubanDesc"></div>
              <div class="doubanIntro">
                <span> 内容简介 · · · · · · </span>
                <p>
                  {{ intro }}
                </p>
              </div>
            </div>
          </el-col>
          <el-col :span="24" :lg="6">
            <div class="moviePlay">
              <div class="playTitle">播放列表</div>
              <div class="playLinks">
                <a
                  v-for="(video, index) in playList"
                  :key="video.index"
                  :href="video.link"
                  ><span>
                    <el-button class="videoButton" type="info">
                      {{ index + 1 }}</el-button
                    >
                  </span>
                </a>
              </div>
            </div>
          </el-col>
          <el-col :span="24"> </el-col>
        </div>
      </transition>
      <el-col v-if="commentsUriInputShow"
        ><el-input
          v-model="commentsPostUri"
          placeholder="eosI8AjOodcKj0hBOB2VmB8VBjRkY09/349"
        ></el-input
        ><el-button type="success" plain @click="postTgComments"
          >提交</el-button
        ></el-col
      ><el-col class="kexuetip" v-if="commentsEnable">
        <span>需要科学查看评论</span>
      </el-col>
      <el-col :span="24" :lg="18" id="comments"> </el-col>
    </el-main>
  </el-container>
</template>
<script type="application/javascript" ></script>
<script>
const axios = require("axios");
import postscribe from "postscribe";
export default {
  name: "movie",
  data: function () {
    return {
      id: "",
      playOptions: [
        { label: "nPlayer" },
        { label: "VLC" },
        { label: "MXPlayer" },
        { label: "MXPlayer Pro" },
        { label: "PotPlayer" },
        { label: "IINA" },
      ],
      playType: "nPlayer",
      videoFiles: [],
      title: "",
      year: "",
      originalTitle: "",
      uri: "",
      intro: "",
      doubanUrl: "",
      thumbnailUrl: "",
      descHtml: "",
      potplayer: `C:\\\\Program Files\\\\DAUM\\\\PotPlayer\\\\PotPlayerMini64.exe`,
      tipVisible: false,
      commentsEnable: false,
      commentsPostUri: "",
      commentsLoaded: false,
      kexueTipShow: false,
      fromIndex: false,
    };
  },
  computed: {
    //播放选项列表
    playList: function () {
      let playList = [];
      if (this.playType === "nPlayer") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `nplayer-${video}`,
          });
        }
      }
      if (this.playType === "MXPlayer") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `intent:${video}#Intent;package=com.mxtech.videoplayer.ad;S.title=New%20title;end`,
          });
        }
      }
      if (this.playType === "MXPlayer Pro") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `intent:${video}#Intent;package=com.mxtech.videoplayer.pro;S.title=New%20title;end`,
          });
        }
      }
      if (this.playType === "VLC") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `vlc://${video}`,
          });
        }
      }
      if (this.playType === "PotPlayer") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `potplayer://${video}`,
          });
        }
      }
      if (this.playType === "IINA") {
        for (let video of this.videoFiles) {
          playList.push({
            link: `iina://weblink?url=${video}`,
          });
        }
      }
      return playList;
    },
    //potplayer注册协议代码
    pcRegCode: function () {
      return `Windows Registry Editor Version 5.00

[HKEY_CLASSES_ROOT\\potplayer]
@="\\"URL:potplayer protocol\\""
"URL Protocol"=""

[HKEY_CLASSES_ROOT\\potplayer\\shell]

[HKEY_CLASSES_ROOT\\potplayer\\shell\\open]

[HKEY_CLASSES_ROOT\\potplayer\\shell\\open\\command]
@="cmd /k ( set \\"var=%1\\" & call set var=%%var:potplayer://=%% & call \\"${this.potplayer}\\" %%var%%)"
`;
    },
    // 完整标题
    fullTitle: function () {
      if (this.originalTitle !== "") {
        return `${this.title} / ${this.originalTitle}（${this.year}）`;
      }
      return `${this.title}（${this.year}）`;
    },
    // 是否显示评论文章输入框
    commentsUriInputShow: function () {
      if (this.commentsEnable && !this.commentsLoaded) {
        return true;
      }
      return false;
    },
  },
  methods: {
    //初始化页面
    init: function () {
      //根据UA显示默认播放器
      if (!this.isPC()) {
        this.playType = "VLC";
      } else {
        this.playType = "PotPlayer";
        if (!localStorage.pc_tip) {
          this.tipVisible = true;
          localStorage.pc_tip = 1;
        }
      }
      if (localStorage.play_type) {
        this.playType = localStorage.play_type;
      }
      // 从路由获取ID
      this.id = this.$route.params.id;
      // 判断是否授权
      if (localStorage.token) {
        this.token = localStorage.token;
        axios.defaults.headers.common["Authorization"] = `JWT ${this.token}`;
      } else {
        this.goBack();
      }
    },
    // 判断UA是否为PC
    isPC: function () {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone|Mac)/i
      );
      return !flag;
    },
    //修改播放选项
    changePlayType: function (value) {
      localStorage.play_type = value;
    },
    //获取电影数据
    getData: function () {
      let vm = this;
      axios
        .get(`/api/movie/${vm.id}`)
        .then(function (response) {
          let data = response.data;
          vm.videoFiles = data.play_links;
          vm.title = data.title;
          vm.year = data.year;
          vm.originalTitle = data.original_title;
          vm.intro = data.intro;
          vm.uri = data.uri;
          vm.doubanUrl = data.douban_url;
          vm.thumbnailUrl = data.thumbnail_url;
          vm.descHtml = data.desc_html;
          if ((data.comments_on = true)) {
            vm.commentsEnable = true;
            vm.commentsPostUri = data.tg_post;
            if (data.tg_post != null) {
              vm.loadComments();
            }
          }
        })
        .catch(function (error) {
          console.log(error);
          vm.goBack();
        });
    },
    // 返回主页
    goBack: function () {
      if (this.fromIndex) {
        this.$router.go(-1);
      } else {
        this.$router.push({ path: "/home" });
      }
    },
    // 提交Telegram评论文章链接
    postTgComments: function () {
      axios
        .post(`/api/movie/${this.id}/comments`, {
          tg_post: this.commentsPostUri,
        })
        .then(function (response) {
          window.location.reload();
        });
    },
    // 加载评论
    loadComments: function () {
      postscribe(
        "#comments",
        `<script async src="https://telegram.org/js/telegram-widget.js?14" data-telegram-discussion="${this.commentsPostUri}"><\/script>`
      );
      this.commentsLoaded = true;
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.fullPath === "/home") {
        vm.fromIndex = true;
      }
    });
  },
  mounted: function () {
    this.init();
    this.getData();
  },
};
</script>

